<template>
    <div class="addModal">
        <div class="modal">
            <i class="fa-solid fa-xmark close-modal" @click="closeAddModal"></i>
            <h2 class="modal-heading">Add Tutorial</h2>
            <span>Add a new tutorial article.</span>
            <form @submit.prevent="addTutorial">
                <div class="form-group">
                    <input 
                    type="text"
                    class="form-control" 
                    v-model="title"
                    placeholder="Title"
                    >
                </div>
                <div class="form-group">
                    <input 
                    type="text"
                    class="form-control" 
                    v-model="url"
                    placeholder="URL"
                    >
                </div>
                <div class="form-group">
                    <button class="btn btn-primary w-100">Add</button>
                </div>
            </form>
        </div>
    </div>
</template>

<script>

import { initializeApp } from 'firebase/app'
import { getFirestore, collection } from 'firebase/firestore'

const firebaseConfig = {
    apiKey: "AIzaSyBMHJcISZ5OP_U1gdGD0MEFpqC4aHAy7T4",
    authDomain: "portfolio-65454.firebaseapp.com",
    projectId: "portfolio-65454",
    storageBucket: "portfolio-65454.appspot.com",
    messagingSenderId: "330355731809",
    appId: "1:330355731809:web:97dc40d4f54b9bfa392920"
}

const app = initializeApp(firebaseConfig)
const db = getFirestore(app)

const tutorialsColRef = collection(db, 'tutorials')

import { addDoc } from 'firebase/firestore'

export default {
  data () {
    return {
      title: null,
      url: null
    }
  },
  methods: {
    async addTutorial () {
      console.log('Creating Document')
      const addedDoc = await addDoc(tutorialsColRef, this.$data)
      alert('Document created successfully!')
      console.log(addedDoc)
      this.$router.push('/tutorials')
    }
  }
}
</script>

